import { Component, OnInit } from '@angular/core';
import { CompanyLettersService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { Letters, Pegawai } from 'src/app/sdk/hr/models';
import { Vendor, Contact } from 'src/app/sdk/asset/models';
import { VendorVendorService, VendorContactService } from 'src/app/sdk/asset/services';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private companyLettersService: CompanyLettersService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private vendorVendorService: VendorVendorService,
        private vendorContactService: VendorContactService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }



    dataLetters: Letters = {}
    dataPegawai: Pegawai = {}
    dataVendor: Vendor = {}


    id_letters: string = ''

    ngOnInit() {
        this.id_letters = this.routerActive.snapshot.paramMap.get('id_letters')
        this.getLisensi();


    }

    getLisensi() {
        this.spinner.show()
        this.companyLettersService.getCompanyLettersId(this.id_letters).subscribe(
            response => {
                this.dataLetters = response.data;
                this.getVendor(this.dataLetters.to)
                this.getPegawai(this.dataLetters.id_pegawai_approval)
                this.getContact(this.dataLetters.contact)
                this.spinner.hide()
            }
        )
    }

    dataContact: Contact = {}
    getContact(contact) {
        this.vendorContactService.getVendorContactsId(contact).subscribe(
            data=> {
                this.dataContact = data.data
            }
        )
    }

    getVendor(to) {
        this.vendorVendorService.getVendorVendorsId(to).subscribe(
            data=> {
                this.dataVendor=data.data
            }
        )
    }

    getPegawai(id_pegawai_approval) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai_approval).subscribe(
            data=> {
                this.dataPegawai=data.data
            }
        )
    }

    goToBack() {

        this.router.navigate(['/hr/company/module/letters/list'])
    }

}
