import { Component, OnInit } from '@angular/core';
import { Letters, Pegawai_master } from 'src/app/sdk/hr/models';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { CompanyLettersService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { FormControl, Validators } from '@angular/forms';
import { Vendor, Contact } from 'src/app/sdk/asset/models';
import { VendorVendorService, VendorContactService } from 'src/app/sdk/asset/services';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyLettersService: CompanyLettersService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private vendorVendorService: VendorVendorService,
        private vendorContactService: VendorContactService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    dataLetters: Letters = {};
    toogleUpload = false
    ngOnInit() {
        this.id_letters = this.activatedRoute.snapshot.paramMap.get('id_letters');
        this.form();
        this.getData()
        this.getPreeData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'company_letters.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )

    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    listPegawai: Pegawai_master[] = [];
    listVendor: Vendor[] = [];
    listContact: Contact[] = [];

    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data;
            }
        )

        this.vendorVendorService.getVendorVendors().subscribe(
            data => {
                this.listVendor = data.data;
            }
        )

    }


    a = 0
    getContact() {
        this.vendorContactService.getVendorContacts(JSON.stringify({ ref_id_module: this.dataForm.value.to })).subscribe(
            data => {
                this.listContact = data.data
                if (this.a > 1) {
                    this.dataForm.get('contact').setValue(null)
                }
                this.a++;
                console.log(this.a)
            }
        )
    }

    id_letters: string = "";
    getData() {
        this.spinner.show()
        this.companyLettersService.getCompanyLettersId(this.id_letters).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
                this.getContact()
                this.singleUploadService.param.next({
                    for_module: "company_letters",
                    for_table: "erp.letters",
                    for_module_id: data.data.id_letters
                })
            }
        )
    }


    dataForm: FormGroupTypeSafe<Letters>;
    form() {
        this.dataForm = this.fb.group<Letters>(
            {
                id_letters: new FormControl('', [Validators.required]),
                id_pegawai_approval: new FormControl('', [Validators.required]),
                to: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                type: new FormControl(''),
                keterangan: new FormControl(''),
                tanggal_dikeluarkan: new FormControl(''),
                status: new FormControl(''),
                nomor: new FormControl(''),
                contact: new FormControl(''),
                id_dokumen: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.dataLetters = this.dataForm.value;
        this.companyLettersService.putCompanyLettersId({
            id: this.id_letters,
            body: this.dataLetters
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/letters/list'])
    }

}
